import React, { useCallback, useEffect, useState } from "react";
import { FILTERS_ENUM, STATUS_OPTIONS } from "shared/constants";
import { docStatusUpdate, statusUpdate } from "../searchBar/filterConsts";
import { useDispatch } from "react-redux";
import { cityListing, countryListing } from "redux/actions/auth";
import ReactSelect from "../ReactSelect";
import './style.scss';

function Filters({
  setCityFilter = () => { },
  setCountryFilter = () => { },
  setStatus = () => { },
  setStatusDoc = () => { },
  toggle,
  setPageCount= () => { },
}) {
  const dispatch = useDispatch();
  const [city, setCity] = useState([]);
  const [country, setCountry] = useState([]);
  const filterButtons = [
    {
      title: "Country",
      data: country,
      id: FILTERS_ENUM.country,
      handleChange: (value) => {
        setPageCount(1);
        setCountryFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "City",
      data: city,
      id: FILTERS_ENUM.city,
      handleChange: (value) => {
        setPageCount(1);
        setCityFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "Activation Status",
      data: statusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatus(value?.value);
      },
    },
    {
      title: "Document Status",
      data: docStatusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatusDoc(value?.value);
      },
    },
  ];

  const getListing = useCallback(() => {
    dispatch(
      cityListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
              value: i.city,
              label: i.city,
            }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCity(temp);
        },
        (err) => {
          // console.log(err?.data?.message, "ERROR CITY");
        }
      )
    );

    dispatch(
      countryListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
              value: i.country,
              label: i.country,
            }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCountry(temp);
        },
        (err) => {
          // console.log(err.data.message, "ERROR COUNTRY");
        }
      )
    );
  }, [dispatch, toggle]);

  useEffect(() => {
    if (toggle) getListing();
  }, [getListing, toggle]);

  return (
    <>
      {toggle ? (
        <div className={`row filters_col ${!toggle ? "d-none" : ""}`}>
          {filterButtons.map((i, idx) => {
            return (
              <div className="col-sm-6 col-md-3 mb-1">
                <label className="d-block text-capitalize">{i.title}</label>
                <ReactSelect
                  options={i.data}
                  handleChange={i.handleChange}
                  selectedOption={i.selected}
                  key={idx}
                  title={"Select"}
                />
              </div>
            );
          })}
        </div>
      ) : null}
    </>
  );
}

export default Filters;
