import React from "react";
import Accordion from "components/atom/Accordion";
import ProfileDescription from "components/atom/ProfileDescription";
import ProfileDetails from "components/atom/ProfileDetails";
import {
  NA,
  PHARMACY_USER_VIEW,
  STATUS_OPTIONS,
  STATUS_UPDATE,
} from "shared/constants";
import CustomTable from "components/atom/table";
import {
  experienceToYears,
  getLocalizedDateString,
  getUniqueDocumentTypes,
  profileCompleted,
} from "components/utility";
import { URL_HEAD } from "shared/api";
import { Images } from "shared/images";
import { Fade, Tooltip } from "@material-ui/core";

const tableJobHeader = [
  "#",
  "Name",
  "Position",
  "Start Date",
  "End Date",
  "Description",
];

const tableEducationHeader = [
  "#",
  "Name",
  "Qualification",
  "Start Date",
  "End Date",
  "Description",
];

const tableHeaderFunc = [
  "#",
  "Document Name",
  "Uploaded Document",
  "Status",
  "Action",
];

function Profile({ data, onUnverifyClick, onVerifyClick, onDocumentView }) {
  const data2 = [
    {
      title: "Name",
      value:
        data?.profile?.first_name && data?.profile?.last_name
          ? (data?.profile?.first_name ? data?.profile?.first_name : "") +
            " " +
            (data?.profile?.last_name ? data?.profile?.last_name : "")
          : NA,
      isVerified: parseInt(data?.verified_document) === 100,
    },
    {
      title: "Phone",
      value: data?.profile?.phone_number ? data?.profile?.phone_number : NA,
    },
    {
      title: "DOB",
      value: data?.profile?.dob
        ? getLocalizedDateString(data?.profile?.dob)
        : NA,
    },
    {
      title: "Email Address",
      value: data?.profile?.email ? data?.profile?.email : NA,
      isVerified: data?.profile?.verified,
    },
    {
      title: "Address",
      value: data?.profile?.address_detail
        ? (data?.profile?.address_detail?.address
            ? data?.profile?.address_detail?.address
            : "") +
          " ," +
          (data?.profile?.address_detail?.city
            ? data?.profile?.address_detail?.city
            : "") +
          " ," +
          (data?.profile?.address_detail?.state
            ? data?.profile?.address_detail?.state
            : "") +
          " ," +
          (data?.profile?.address_detail?.country
            ? data?.profile?.address_detail?.country
            : "") +
          " ," +
          (data?.profile?.address_detail?.postal_code
            ? data?.profile?.address_detail?.postal_code
            : "")
        : NA,
    },
    {
      title: "Registration Number",
      value: data?.profileVerification?.reg_num
        ? data?.profileVerification?.reg_num
        : NA,
    },
    {
      title: "Insurance Valid Till",
      value: data?.profileVerification?.valid_till
        ? getLocalizedDateString(data?.profileVerification?.valid_till)
        : NA,
    },
    {
      title: "Total Experience",
      value:
        experienceToYears(data?.experience?.years, data?.experience?.months) +
        " Years",
    },
  ];

  const ButtonVerifiedUnverified = ({ isverified, documentType, id, docs }) => {
    if (isverified === STATUS_OPTIONS.UNVERIFIED.key) {
      return (
        <button
          className="btn btn-sm btn-danger m-1"
          onClick={() => {
            onUnverifyClick(id, documentType, docs);
          }}
        >
          {STATUS_OPTIONS.UNVERIFY.title}
        </button>
      );
    } else {
      return (
        <button
          className="btn btn-sm btn-secondary m-1"
          onClick={() => onVerifyClick(documentType)}
        >
          {STATUS_OPTIONS.VERIFY.title}
        </button>
      );
    }
  };

  return (
    <div className="tab_content">
      <ProfileDetails
        data={data2}
        imageURL={
          data?.profile?.photo
            ? URL_HEAD + data?.profile?.photo
            : Images.defaultUserPic
        }
        rating={3}
        profileCompleteness={profileCompleted(
          data?.profile_completeness,
          data?.verified_document
        )}
      />
      {data?.profile?.personal_description ? (
        <ProfileDescription
          title={"Description"}
          description={
            data?.profile?.personal_description
              ? data?.profile?.personal_description
              : NA
          }
        />
      ) : null}

      <section className="accordin_sec">
        <Accordion title={"Education"}>
          {data?.cvManage?.edu_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableEducationHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.cvManage?.edu_history?.length ? (
                    data?.cvManage?.edu_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.institution_name} </td>
                            <td>{i?.qualification} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                  ) : (
                    <tr>
                      <td colSpan={6} className="text-center py-5">
                        No Education Details Uploaded
                      </td>
                    </tr>
                  )}
                </tbody>
              </CustomTable>
              {/* <BasicTable
                    theadData={tableHeader}
                    tbodyData={
                      data?.edu_history?.length ? data?.edu_history : []
                    }
                  /> */}
            </div>
          ) : (
            <span className="no_result py-3">
              {"No Education Details Uploaded"}{" "}
            </span>
          )}
        </Accordion>
        <Accordion title={"Experience"}>
          {data?.cvManage?.job_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableJobHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.cvManage?.job_history?.length ? (
                    data?.cvManage?.job_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.company_name} </td>
                            <td>{i?.job_title} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                  ) : (
                    <tr>
                      <td colSpan={6} className="text-center py-5">
                        No Experience Details Uploaded
                      </td>
                    </tr>
                  )}
                </tbody>
              </CustomTable>
            </div>
          ) : (
            <span className="no_result py-3">
              {"No Experience Details Uploaded"}{" "}
            </span>
          )}
        </Accordion>
      </section>

      <div className="card">
        <div className="card-body">
          <h6 className="h6 text-captialize">Uploaded Documents</h6>

          {data?.profileVerification?.documents?.length ? (
            <CustomTable>
              <thead>
                <tr key={1000 + 1}>
                  {tableHeaderFunc.map((i, idx) => (
                    <th scope="col" id={idx}>
                      {i}
                    </th>
                  ))}
                </tr>
              </thead>

              {data?.profileVerification?.documents?.length ? (
                getUniqueDocumentTypes(
                  data?.profileVerification?.documents
                )?.map((i, idx) => {
                  const documentType = i.document_type;
                  const title =
                    PHARMACY_USER_VIEW.DOCUMENT_TYPE_DATA[documentType]?.title;
                  return (
                    <>
                      <tbody>
                        <tr>
                          <td>{idx + 1} </td>
                          <td>{title ? title : NA} </td>
                          <td
                            onClick={() => {
                              onDocumentView(
                                i?.id,
                                documentType,
                                getUniqueDocumentTypes(
                                  data?.profileVerification?.documents
                                )
                              );
                            }}
                          >
                            <span className="badge btn-sm badge-grey">
                              {i?.document ? i?.name : "Not Uploaded"}
                            </span>
                          </td>
                          <td>
                            <div className="d-flex align-items-center">
                              <span
                                className={`m-1 badge-md badge-${
                                  i?.verified === STATUS_OPTIONS.PENDING.key
                                    ? "info"
                                    : i?.verified === STATUS_OPTIONS.VERIFY.key
                                    ? "success"
                                    : i?.verified ===
                                      STATUS_OPTIONS.UNVERIFY.key
                                    ? "danger"
                                    : "grey"
                                }`}
                              >
                                {STATUS_UPDATE[i?.verified]}
                              </span>

                              {i?.verified === STATUS_OPTIONS.UNVERIFY.key ? (
                                <span className="position-relative">
                                  <span className="ms-2 tool_tip">
                                    <div className="tooltiptext">
                                      {i?.comment ? i?.comment : NA}
                                    </div>
                                    <svg
                                      width="26"
                                      height="26"
                                      viewBox="0 0 26 26"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        fill-rule="evenodd"
                                        clip-rule="evenodd"
                                        d="M12.8428 25.0869C19.5628 25.0869 25.0107 19.639 25.0107 12.9189C25.0107 6.19891 19.5628 0.750977 12.8428 0.750977C6.12274 0.750977 0.674805 6.19891 0.674805 12.9189C0.674805 19.639 6.12274 25.0869 12.8428 25.0869ZM12.8428 22.8747C18.3411 22.8747 22.7984 18.4173 22.7984 12.9191C22.7984 7.42089 18.341 2.96348 12.8428 2.96348C7.34457 2.96348 2.88716 7.42089 2.88716 12.9191C2.88716 18.4173 7.34457 22.8747 12.8428 22.8747ZM14.7785 7.6682C14.7785 8.12634 14.6211 8.51754 14.3048 8.83932C13.989 9.16243 13.6084 9.3237 13.1638 9.3237C12.7174 9.3237 12.3362 9.16243 12.0168 8.83932C11.6981 8.51756 11.5384 8.12633 11.5384 7.6682C11.5384 7.21064 11.6981 6.81866 12.0168 6.49303C12.3354 6.16722 12.7174 6.00518 13.1638 6.00518C13.6084 6.00518 13.989 6.16799 14.3048 6.49303C14.6211 6.81866 14.7785 7.21062 14.7785 7.6682ZM14.6202 18.5789C14.1452 18.8071 13.7654 18.9807 13.4826 19.1003C13.1998 19.2201 12.8707 19.2795 12.4961 19.2795C11.9207 19.2795 11.4729 19.1082 11.1537 18.7676C10.8344 18.4259 10.6747 17.9923 10.6747 17.4668C10.6747 17.2631 10.6865 17.0538 10.7106 16.8406C10.7347 16.6275 10.7733 16.3865 10.8259 16.1178L11.4203 13.5609C11.4729 13.3159 11.5179 13.0837 11.5537 12.8642C11.5902 12.6462 11.608 12.4458 11.608 12.265C11.608 11.9384 11.5524 11.7102 11.4417 11.5817C11.3309 11.4533 11.1199 11.3885 10.8065 11.3885C10.6527 11.3885 10.4949 11.4186 10.334 11.4764C10.1724 11.5343 10.0773 10.8541 10.0773 10.8541C10.4664 10.6616 10.8383 10.4966 11.1942 10.3595C11.5499 10.2223 11.8861 10.1535 12.204 10.1535C12.7756 10.1535 13.2166 10.3215 13.5261 10.6575C13.8356 10.9935 13.9907 11.4295 13.9907 11.9669C13.9907 12.0779 13.9802 12.2737 13.9588 12.5534C13.9372 12.834 13.8975 13.0908 13.8396 13.3246L13.248 15.8721C13.1998 16.0765 13.156 16.3103 13.1182 16.5734C13.0792 16.835 13.0603 17.0349 13.0603 17.1688C13.0603 17.5073 13.1222 17.7378 13.2466 17.8607C13.3716 17.9836 13.5873 18.0445 13.8937 18.0445C14.0378 18.0445 14.2012 18.0137 14.3831 17.9527C14.5648 17.8916 14.6202 18.5789 14.6202 18.5789Z"
                                        fill="#FC5252"
                                      />
                                    </svg>
                                  </span>
                                   {/* <Tooltip
                                    title={i?.comment ? i?.comment : NA}
                                    arrow={true}
                                    placement={"bottom"}
                                    enterDelay={200}
                                    TransitionComponent={Fade}
                                    TransitionProps={{ timeout: 300 }}
                                  >
                                    <svg
                                      width="25"
                                      height="25"
                                      viewBox="0 0 25 25"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        fill-rule="evenodd"
                                        clip-rule="evenodd"
                                        d="M12.168 24.3359C18.888 24.3359 24.3359 18.888 24.3359 12.168C24.3359 5.44793 18.888 0 12.168 0C5.44793 0 0 5.44793 0 12.168C0 18.888 5.44793 24.3359 12.168 24.3359ZM12.168 22.1237C17.6663 22.1237 22.1236 17.6663 22.1236 12.1681C22.1236 6.66991 17.6662 2.21251 12.168 2.21251C6.66976 2.21251 2.21236 6.66991 2.21236 12.1681C2.21236 17.6663 6.66976 22.1237 12.168 22.1237ZM14.1037 6.91722C14.1037 7.37536 13.9463 7.76656 13.63 8.08834C13.3142 8.41145 12.9336 8.57272 12.489 8.57272C12.0426 8.57272 11.6614 8.41145 11.3419 8.08834C11.0233 7.76658 10.8636 7.37535 10.8636 6.91722C10.8636 6.45966 11.0233 6.06769 11.3419 5.74206C11.6606 5.41624 12.0426 5.25421 12.489 5.25421C12.9336 5.25421 13.3142 5.41702 13.63 5.74206C13.9463 6.06768 14.1037 6.45964 14.1037 6.91722ZM13.9454 17.8279C13.4704 18.0561 13.0906 18.2297 12.8078 18.3493C12.525 18.4691 12.1959 18.5285 11.8213 18.5285C11.2459 18.5285 10.7981 18.3572 10.4789 18.0166C10.1596 17.675 9.9999 17.2413 9.9999 16.7158C9.9999 16.5121 10.0117 16.3028 10.0358 16.0897C10.0599 15.8765 10.0985 15.6356 10.1511 15.3668L10.7455 12.8099C10.7981 12.565 10.8431 12.3327 10.8789 12.1132C10.9154 11.8952 10.9331 11.6948 10.9331 11.514C10.9331 11.1874 10.8776 10.9593 10.7669 10.8308C10.6561 10.7023 10.4451 10.6375 10.1317 10.6375C9.97791 10.6375 9.82012 10.6676 9.65924 10.7254C9.49759 10.7833 9.40248 10.1031 9.40248 10.1031C9.79157 9.9106 10.1635 9.74567 10.5194 9.6085C10.8751 9.47135 11.2113 9.40248 11.5292 9.40248C12.1008 9.40248 12.5418 9.57049 12.8513 9.90653C13.1608 10.2426 13.3158 10.6785 13.3158 11.216C13.3158 11.3269 13.3054 11.5227 13.284 11.8024C13.2624 12.0831 13.2227 12.3398 13.1648 12.5736L12.5732 15.1211C12.525 15.3256 12.4812 15.5594 12.4434 15.8225C12.4044 16.084 12.3855 16.2839 12.3855 16.4178C12.3855 16.7563 12.4474 16.9868 12.5718 17.1097C12.6968 17.2326 12.9125 17.2936 13.2189 17.2936C13.363 17.2936 13.5263 17.2627 13.7083 17.2017C13.89 17.1406 13.9454 17.8279 13.9454 17.8279Z"
                                        fill="#FC5252"
                                      />
                                    </svg>
                                  </Tooltip> */}
                                </span> 
                              ) : null}
                            </div>
                          </td>
                          <td width={230}>
                            <div className="d-flex">
                              {i?.verified === STATUS_OPTIONS.VERIFY.key ? (
                                <button
                                  className="btn btn-sm btn-danger m-1"
                                  onClick={() => {
                                    onUnverifyClick(
                                      i?.id,
                                      documentType,
                                      getUniqueDocumentTypes(
                                        data?.profileVerification?.documents
                                      )
                                    );
                                  }}
                                  disabled={
                                    i?.verified === STATUS_OPTIONS.UNVERIFY.key
                                  }
                                >
                                  {STATUS_OPTIONS.UNVERIFY.title}
                                </button>
                              ) : i?.verified ===
                                STATUS_OPTIONS.UNVERIFY.key ? (
                                <button
                                  className="btn btn-sm btn-secondary m-1"
                                  onClick={() => onVerifyClick(documentType)}
                                  disabled={
                                    i?.verified === STATUS_OPTIONS.VERIFY.key
                                  }
                                >
                                  {STATUS_OPTIONS.VERIFY.title}
                                </button>
                              ) : (
                                <>
                                  <button
                                    className="btn btn-sm btn-secondary m-1"
                                    onClick={() => onVerifyClick(documentType)}
                                    disabled={
                                      i?.verified === STATUS_OPTIONS.VERIFY.key
                                    }
                                  >
                                    {STATUS_OPTIONS.VERIFY.title}
                                  </button>
                                  <button
                                    className="btn btn-sm btn-danger m-1"
                                    onClick={() => {
                                      onUnverifyClick(
                                        i?.id,
                                        documentType,
                                        getUniqueDocumentTypes(
                                          data?.profileVerification?.documents
                                        )
                                      );
                                    }}
                                    disabled={
                                      i?.verified ===
                                      STATUS_OPTIONS.UNVERIFY.key
                                    }
                                  >
                                    {STATUS_OPTIONS.UNVERIFY.title}
                                  </button>
                                </>
                              )}
                            </div>
                          </td>
                        </tr>
                      </tbody>
                    </>
                  );
                })
              ) : (
                <tbody>
                  <tr>
                    <td colSpan={5} className="text-center py-5">
                      No Documents Uploaded
                    </td>
                  </tr>
                </tbody>
              )}
            </CustomTable>
          ) : (
            <span className="no_result py-3">{"No Documents Uploaded"} </span>
          )}
        </div>
      </div>
    </div>
  );
}

export default Profile;
