import { NOTIFICATION_DATA } from "../actions/actionType";

const initialState = {
  notificationData: [],
};

function persistReducer(state = initialState, action) {
  switch (action.type) {
      case NOTIFICATION_DATA:
      return {
        ...state,
        notificationData: state.notificationData.push(action.payload),
      }
    default:
      return state;
  }
}

export default persistReducer;
