import React from "react";
import Websocket from "react-websocket";
import { API } from "shared/api";

function ReactWebSockets({ onNotification }) {
  const handleChange = (value) => {
    onNotification();
  };
  return (
    <>
      <Websocket
        url={API.SOCKET}
        onMessage={handleChange}
        onClose={(e) => console.log(e, "ON CLOSE SOCKET")}
        onOpen={(e) => console.log(e, "ON OPEN SOCKET")}
      />
    </>
  );
}

export default ReactWebSockets;
